import {
    initGlobalEventListeners,
    getSelectedProject,
    authStateObserver,
} from './modules/todoList';
import { renderModals, renderProjects, renderProjectView } from './modules/dom';
import './styles/styles.css';
import { initFirebaseAuth } from './modules/firebase';

init();

function init() {
    renderModals();
    renderProjects();
    renderProjectView(getSelectedProject());
    initGlobalEventListeners();
    initFirebaseAuth(authStateObserver);
}
