// Import the functions you need from the SDKs you need
import { initializeApp } from 'firebase/app';
import {
    getAuth,
    GoogleAuthProvider,
    onAuthStateChanged,
    signInWithPopup,
    signOut,
} from 'firebase/auth';

// Your web app's Firebase configuration
const firebaseConfig = {
    apiKey: 'AIzaSyB0CHkgPeFaS2Cm9qGUo4t6biN12DW9GhM',
    authDomain: 'todolist-55332.firebaseapp.com',
    projectId: 'todolist-55332',
    storageBucket: 'todolist-55332.appspot.com',
    messagingSenderId: '244949175250',
    appId: '1:244949175250:web:52dc4fe0d784ba7bffd60b',
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);

// Initialize Firebase Authentication and get a reference to the service
const auth = getAuth(app);
const provider = new GoogleAuthProvider();

export async function signIn() {
    await signInWithPopup(auth, provider);
}

export function signOutUser() {
    signOut(auth);
}

export function initFirebaseAuth(authStateObserver) {
    onAuthStateChanged(auth, authStateObserver);
}
